import PropTypes from 'prop-types'
import TextBlock from './TextBlock'
import { Image, CloudinaryContext, Transformation } from 'cloudinary-react'

import './sideHug.scss'

const SideHug = ( props ) => {
  const { imageFirst, textData, image, googleMap } = props.data 

  console.log('googleMap: ', googleMap)

  return (
    <section className={`side-hug`}>
      <div className={`container flex`}>
        <div className={`col`}>
          {imageFirst ? 
            (
              // display google map if we have one
              googleMap && (
              <div>{googleMap}</div>
              ),

              // display image if we have one
              image && (
                <CloudinaryContext cloudName="tpierce36">
                  <div className={`img-wrapper`}>
                    <Image 
                      publicId={image.path}
                      responsive
                      width="auto"
                      crop="scale"
                    >
                      <Transformation quality="auto" fetchFormat="auto" />
                    </Image>
                  </div>
                </CloudinaryContext>
              )
            ) : (
              textData.map( (text, index) => (
                <TextBlock key={index} data={text} />
              ))
            )
          }
        </div>
        <div className={`col`}>
          {imageFirst ?
            (
              textData.map( (text, index) => (
                <TextBlock key={index} data={text} />
              ))
            ) : (
              // display google map if we have one
              googleMap && (
                <div>{googleMap}</div>
              ),

              // display image if we have one
              image && (
                <CloudinaryContext cloudName="tpierce36">
                  <div className={`img-wrapper`}>
                    <Image 
                      publicId={image.path}
                      responsive
                      width="auto"
                      crop="scale"
                    >
                      <Transformation quality="auto" fetchFormat="auto" />
                    </Image>
                  </div>
                </CloudinaryContext>
              )
            )
          }
        </div>
      </div>
    </section>
  )
}

SideHug.propTypes = {
  data: PropTypes.object,
}

export default SideHug